@import "../defaults";   // Global defaults.

// hpv24 Equal height layout for bootstrap rows.
// -----------------------------------------------------------------------------

.equal-height {
  .row {
    display: flex;
    flex-wrap: wrap;

    & > div[class*='col-'] {
      display: flex;
      flex-direction: column;

      article.node {
        height: 100%;
        div.main-content {
          height: 100%;
          div.card {
            height: 100%;
          }
        }
      }
    }
  }
}

